@import url(https://fonts.googleapis.com/css?family=Rubik:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home{background:#8a2387;animation:bgcolor 10s infinite;animation-direction:alternate;min-height:calc(100vh - 56px);background-repeat:no-repeat;text-align:center;display:flex;align-items:flex-start;justify-content:center}@keyframes bgcolor{0%{background-color:#3498db}50%{background-color:#27ae60}100%{background-color:#e67e22}}.home h1{padding-top:30px;display:inline-block;color:#2c3e50;max-width:80%}.home p{padding-right:10px;padding-left:10px}.home .info-box{border-radius:6px;width:800px;max-width:80%;margin-top:80px;padding-bottom:30px;box-shadow:0 30px 30px rgba(0,0,0,0.3),0 15px 12px rgba(0,0,0,0.22);background-color:rgba(255,255,255,0.7);animation:fadein 0.5s;animation-timing-function:ease-out}@keyframes fadein{from{opacity:0}to{opacity:1}}.home .react-autosuggest__container{width:60%}.home .react-autosuggest__input{width:100%;height:40px;margin:10px 0px 10px 0px}.home .react-autosuggest__suggestions-container--open{display:block;position:absolute;top:49px;width:100%}footer{padding-top:1.5rem;padding-bottom:1rem}

.search-wrapper{position:relative;display:flex;align-items:center;justify-content:center}.react-autosuggest__container{position:relative;display:flex;align-items:center;justify-content:center;display:inline-block}.react-autosuggest__input{width:240px;height:40px;padding:10px 20px;font-family:Helvetica, sans-serif;font-weight:300;font-size:16px;border:1px solid #aaa;border-right:0px;border-radius:4px;border-top-right-radius:0;border-bottom-right-radius:0;margin:20px 0px 20px 0px}.react-autosuggest__input--focused{outline:none;border-color:#95a5a6;box-shadow:inset 0 1px 1px rgba(0,0,0,0.075),0 0 8px rgba(149,165,166,0.6)}.react-autosuggest__input--open{border-bottom-left-radius:0;border-bottom-right-radius:0}.react-autosuggest__suggestions-container{display:none}.react-autosuggest__suggestions-container--open{display:block;position:absolute;top:59px;width:240px;border:1px solid #aaa;background-color:#fff;font-family:Helvetica, sans-serif;font-weight:300;font-size:16px;border-bottom-left-radius:4px;border-bottom-right-radius:4px;z-index:2}.location-btn{border-top-left-radius:0;border-bottom-left-radius:0;height:40px}.react-autosuggest__suggestions-list{margin:0;padding:0;list-style-type:none}.react-autosuggest__suggestion{cursor:pointer;padding:10px 20px}.react-autosuggest__suggestion--highlighted{background-color:#ddd}

.nearest-stop-modal{text-align:center}.showMoreBtn{margin-top:15px;padding:0rem 0.3rem}

.info-modal{text-align:center}.info-modal .modal-content{border:none}.info-modal .map-image{border-radius:5px;width:50%}.info-modal .map-image.full{width:100%}.info-modal .stop-info{margin-top:10px;margin-bottom:10px}.info-modal .expand-text{display:block;color:#007bff;text-decoration:none;background-color:transparent;cursor:pointer;text-decoration:none}.info-modal .expand-text:hover{text-decoration:underline}

.tracking-page{}.tracking-page h1,.tracking-page h4{text-align:center}.tracking-page h4{font-family:'Rubik', sans-serif}.tracking-page .errors{margin-top:10px;text-align:center;white-space:pre-line}.tracking-page .no-bus{margin-left:20px;margin-right:20px}.tracking-page .table{font-family:'Rubik', sans-serif}.tracking-page .table td{padding-top:2rem;padding-bottom:2rem;font-size:20px;border-top:none;font-family:'Lato', sans-serif}@media screen and (max-width: 500px){.tracking-page .table td{font-size:5vw}.tracking-page .table td .btn{font-size:12px}}.tracking-page .no-wrap{word-break:keep-all !important}.tracking-page .stop_name{margin-top:20px}.tracking-page .refresh-text{text-align:center;opacity:0;transition:opacity 0.75s}.tracking-page .refresh-text h5{font-weight:400}@media screen and (max-width: 500px){.tracking-page .refresh-text h5{font-size:3.5vw}}.tracking-page .fadeIn{opacity:1}.tracking-page .fadeOut{opacity:0;transition:none}.tracking-page .refresh-btn{color:#3498db;outline:none;background-color:transparent;border:none;padding:0px;transition:color 0.5s}.tracking-page .refresh-btn:hover{color:#2980b9;outline:none}

